.inputForm{
    background-color: aqua;
    height: 400px;
    width: 400px;
    margin-left: 430px;
    box-shadow: 1px 6px 46px  0px rgba(54,54,54,0,75);
    margin-top: 50px;
    padding: 20px;
    border-radius: 5px;
}
.inputForm input,textarea{
    width: 300px;
    border: 1px solid white;
    padding: 10px 10px;
    outline: none;
    margin-bottom: 20px;
    font-size: 16px;

}
.inputForm button
{
    background-color: blueviolet;
    color: white;
    border: none;
    padding: 5px 25px;
    font-size: 14px;
    font-weight: bold;

}
